.App {
  box-sizing: border-box;
  text-align: center;
  display: flex;
  background-color: #fff4df;
  min-height: 100%;
  justify-content: center;
  align-items: center;
  position: relative;
  z-index: 5;
  padding: 10px;
}

.input {
  width: 400px;
  height: 400px;
  z-index: 12;
  margin-right: -10px;
}

.input .header {
  height: 100px;
}

.input textarea {
  position: relative;
  width: calc(100% - 10px);
  overflow-y: scroll;
  display: block;
  flex: 1;
  resize: none;
  border: none;
  outline: none;
  background:none;
}

textarea:after {
  content: ' ';
  border:1px solid #f30;
  display: inline-block;
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: red;
  top: 0;
  left: 15px;
  z-index: 13;
}

.output {
  width: 500px;
  height: 500px;
  overflow: scroll;
  text-align: start;
}

.output .header {
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.header .search {
  height: 12px;
  margin: auto 0;
  margin-left: 10px;
  flex: 1;
  border: 2px solid #0c0c0d;
  border-radius: 10px;
}
.output .box {
  box-sizing: border-box;
  flex: 1;
  overflow-y: scroll;
  padding-left: 10px;
  margin: 3px;
}

table {
  border-radius: 10px;
  border: 2px solid #0c0c0d;
  border-spacing: 0;
  border-collapse: collapse;
}

th, td {
  border:  2px solid #0c0c0d;
  margin: 0;
  padding: 5px 10px;

}

blockquote {
  display: flex;
  position: relative;
}

blockquote::before {
  content: '.';
  position: absolute;
  width: 4px;
  height: 100%;
  top: 0;
  left:-20px;
  background: #0c0c0d;
  border-radius: 10px;
}

#dialog {
  background-color: #ffffff;

  position: absolute;
  top: calc(50% - 150px);
  left: calc(50% - 150px);
  width: 300px;
  height: 300px;
  z-index: 15;
}

#backdrop {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.1);
  z-index: 11;
}

.dots {
  display: grid;
  grid-template-columns: min-content min-content min-content;
  grid-gap: 2px;
}

.dots > div {
  display: block;
  background-color: #ffffff;
  border-radius: 100%;
  width: 12px;
  height: 12px;
  border: 2px solid #0c0c0d;
}

.window {
  overflow: hidden;
  border: 2px solid #0c0c0d;
  box-sizing: border-box;
  box-shadow: -4px 5px 0px #0c0c0d;
  border-radius: 9px;
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
}

.header {
  box-sizing: border-box;
  border-bottom: 2px solid #0c0c0d;
  padding: 5px 10px;
}

.infos li {
  list-style: none;
  display: flex;
  flex-direction: column;
}

.infos li button {
  background: none;
  outline: none;
  border: none;
}

.infos li img {
  height: 30px;
  text-shadow: 2px 2px;
  filter: drop-shadow(-1px 2px 0px #0C0C0D);
}

.infos li a {
  text-decoration: none;
  color: #0c0c0d;
}

::-webkit-scrollbar {
  width: 15px;
  margin-top: 10px;
  background: none;

}
::-webkit-scrollbar-track {
  width: 15px;
  margin: 5px 0;
  background: none;
}
::-webkit-scrollbar-thumb {
  border-radius: 10px;
  border: 2px solid #0c0c0d;
}
@media screen and (max-width: 748px) {
  .App {
    display: block;
    align-items: center;
  }
  .output {
    width: 100%;
    height: 400px;
    overflow: scroll;
    text-align: start;
  }
  .input {
    width: 95%;
    margin: 0 auto;
  }

  .input textarea {
    width: 100%;
  }

  ul {
    display: flex;
    list-style: none;
  }
}
